@-webkit-keyframes fx-mask {
0% {
    -webkit-transform: translateX(-100%) rotate(45deg);
            transform: translateX(-100%) rotate(45deg);
    -webkit-filter: blur(5px);
            filter: blur(5px);
}
100% {
    -webkit-transform: translateX(100%) rotate(45deg);
            transform: translateX(100%) rotate(45deg);
    -webkit-filter: blur(5px);
            filter: blur(5px);
}
}
@keyframes fx-mask {
0% {
    -webkit-transform: translateX(-100%) rotate(45deg);
            transform: translateX(-100%) rotate(45deg);
    -webkit-filter: blur(5px);
