            transform: translateX(100%) rotate(45deg);
    -webkit-filter: blur(5px);
            filter: blur(5px);
}
}
@-webkit-keyframes fx-text {
0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
}
100% {
    -webkit-transform: translateX(1em);
